.map {
	height: 500px;
	background-color: white;
	padding: 1rem;
	border-radius: 20px;
	margin-top: 16px;
	box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}

.map .leaflet-container {
	height: 100%;
}

.info-flag {
	height: 80px;
	width: 100%;
	background-size: cover;
	border-radius: 8px;
}

.info-flag img {
	width: 100px;
	border-radius: 5px;
}

.info-container {
	width: 150px;
}

.info-confirmed,
.info-recovered,
.info-deaths {
	font-size: 15px;
	margin-top: 5px;
}

.info-confirmed {
	color: blue;
}
.info-recovered {
	color: rgb(0, 192, 0);
}
.info-deaths {
	color: red;
}

.info-name {
    margin-top: 5px;
	font-size: 20px;
	font-weight: bold;
	color: #555;
}
